import { mapState, mapMutations } from 'vuex';
export default {
	computed:{
		...mapState(["code"])
	},
	mounted(){
		uni.setNavigationBarTitle({
		　　title:'健康生活'
		})
	},
	methods:{
		...mapMutations(["setCode"]),
		wechat_login(){
			let _this = this;
			uni.login({
				provider:"weixin",
				success:res=>{
					if(res.errMsg == "login:ok"){
						_this.$state.commit("setCode", res.code);
					}
				}
			})
		},
		getUserInfo(e){
			console.log(e)
		},
		phone_login(){
			uni.navigateTo({
				url:"./phoneLogin"
			})
		},
		goToClause(){
			uni.navigateTo({
				url:"./clause"
			})
		}
	}
}
